import { Component, OnInit } from '@angular/core';
import { SmsUpcomingService } from '../../../../../../services/sms-upcoming/sms-upcoming.service';
import { SmsNotice } from '../../../../../../class/sms-notice';

@Component({
  selector: 'app-page-upcoming',
  templateUrl: './page-upcoming.component.html',
  styleUrls: ['./page-upcoming.component.css']
})
export class PageUpcomingComponent implements OnInit {
  list: SmsNotice[] = [];

  constructor(private upcoming: SmsUpcomingService) {
    upcoming.getList(list => {
      this.list = list as SmsNotice[];
    });
  }

  ngOnInit() {
  }

}



