import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PageUserComponent } from './page-user.component';
import { PageInfoComponent } from './pages/page-info/page-info.component';
import { PageSettingComponent } from './pages/page-setting/page-setting.component';
import { PageAboutComponent } from './pages/page-about/page-about.component';

const userRoutes: Routes = [
  {
    path: 'user',
    component: PageUserComponent,
    children: [
      { path: 'info', component: PageInfoComponent },
      { path: 'about', component: PageAboutComponent },
      { path: 'setting', component: PageSettingComponent }
    ]
  }
];

@NgModule({
  imports: [
    RouterModule.forChild(userRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class UserRoutingModule { }



