import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PageHomeComponent } from './page-home.component';
import { PageSmsComponent } from './pages/page-sms/page-sms.component';
import { PageWorkComponent } from './pages/page-work/page-work.component';
import { PageUserComponent } from './pages/page-user/page-user.component';
import { PageUpcomingComponent } from './pages/page-sms/pages/page-upcoming/page-upcoming.component';
import { PageNoticeComponent } from './pages/page-sms/pages/page-notice/page-notice.component';

const homeRoutes: Routes = [
  {
    path: 'home',
    component: PageHomeComponent,
    children: [
      { path: 'work', component: PageWorkComponent },
      { path: 'user', component: PageUserComponent },
      {
        path: 'sms',
        component: PageSmsComponent,
        children: [
          { path: 'upcoming', component: PageUpcomingComponent },
          { path: 'notice', component: PageNoticeComponent },
          { path: '',
              redirectTo: '/home/sms/upcoming',
              pathMatch: 'full'
          }
        ]
      },
      { path: '',
          redirectTo: '/home/sms/upcoming',
          pathMatch: 'full'
      }
    ]
  }
];

@NgModule({
  imports: [
    RouterModule.forChild(homeRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class HomeRoutingModule { }



