import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { AngularSvgIconModule } from 'angular-svg-icon';
import { SettingService } from '../../services/setting/setting.service';
import { SmsNoticeService } from '../../services/sms-notice/sms-notice.service';
import { SmsUpcomingService } from '../../services/sms-upcoming/sms-upcoming.service';

import { PageSmsComponent } from './pages/page-sms/page-sms.component';
import { PageWorkComponent } from './pages/page-work/page-work.component';
import { PageUserComponent } from './pages/page-user/page-user.component';
import { PageUpcomingComponent } from './pages/page-sms/pages/page-upcoming/page-upcoming.component';
import { PageNoticeComponent } from './pages/page-sms/pages/page-notice/page-notice.component';
import { SegmentedControlComponent } from './components/segmented-control/segmented-control.component';
import { MenuListComponent } from '../../components/menu-list/menu-list.component';

@NgModule({
  imports: [
    CommonModule,
    RouterModule,
    AngularSvgIconModule
  ],
  providers: [
    SettingService,
    SmsNoticeService,
    SmsUpcomingService
  ],
  declarations: [
    PageSmsComponent,
    PageWorkComponent,
    PageUserComponent,
    PageUpcomingComponent,
    PageNoticeComponent,
    SegmentedControlComponent,
    MenuListComponent
  ]
})
export class HomeModule { }



