import { Injectable } from '@angular/core';
// import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { SmsNotice } from '../../class/sms-notice';

@Injectable()
export class SmsUpcomingService {
  private getUrl = 'api/getsmsupcoming.php';

  constructor (private http: HttpClient) {
  }

  /**
   * 获取待办事项列表
   */
  getList(callback: Function) {
    this.http.get<SmsNotice[]>(this.getUrl)
      .subscribe(data => {
        callback(data);
      });
  }

}



