import { Injectable } from '@angular/core';
import { User } from '../../class/user';
import { MenuTab } from '../../class/menu-tab';
import { SmsSegmented } from '../../class/sms-segmented';

import * as dd from 'dingtalk.js';

@Injectable()
export class SettingService {
  private tabs: MenuTab[];
  private userMenus: MenuTab[];
  private smsSegmenteds: SmsSegmented[];
  private navTitle: String;
  private title: String;
  private currentUser: User;

  constructor() {
    this.title = this.navTitle = '易协同1.0';

    this.tabs = [
      {title: '消息', icon: 'sms', url: '/home/sms'},
      {title: '工作台', icon: 'work', url: '/home/work'},
      {title: '我', icon: 'user', url: '/home/user'}
    ];

    this.userMenus = [
      {title: '个人信息', icon: 'info', url: '/user/info', color: '#0099CC'},
      {title: '设置', icon: 'setting', url: '/user/setting', color: '#0099CC'},
      {title: '关于', icon: 'about', url: '/user/about', color: '#0099CC'}
    ];

    this.smsSegmenteds = [
      {name: '待办', badge: 4, url: '/home/sms/upcoming'},
      {name: '通知', badge: 6, url: '/home/sms/notice'}
    ];

    this.currentUser = {
      id: 0,
      name: '敖 博',
      isAvatar: false,
      avatar: 'avatar'
    };
  }

  /**
   * 获取页面标题
   */
  getTitle(): String {
    return this.navTitle;
  }

  /**
   * 判断标题是否是默认标题
   */
  isTitle(): Boolean {
    return this.navTitle === this.title;
  }

  /**
   * 设置标页面题
   * @param title 标题,为空是即恢复默认标题
   */
  setTitle( title: String = this.title) {
    if (dd.version !== null) {
      dd.biz.navigation.setTitle({
        title : title,
        onSuccess : function(result) {
          console.error(result);
        },
        onFail : function(err) {
          console.error(err);
        }
      });
    }
    this.navTitle = title;
  }

  /**
   * 获取底部导航栏菜单列表
   */
  getTabBarNames(): MenuTab[] {
    return this.tabs;
  }

  /**
   * 获取‘我’个人菜单列表
   */
  getUserMenus(): MenuTab[] {
    return this.userMenus;
  }

  /**
   * 获取消息页面分段器列表
   */
  getSmsSegmenteds(): SmsSegmented[] {
    return this.smsSegmenteds;
  }

  /**
   * 获取当前登录用户信息
   */
  getUser(): User {
    return this.currentUser;
  }

}



