import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { SvgIconRegistryService } from 'angular-svg-icon';
import { MenuTab } from '../../class/menu-tab';

@Component({
  selector: 'app-tab-bar',
  templateUrl: './tab-bar.component.html',
  styleUrls: ['./tab-bar.component.css']
})
export class TabBarComponent implements OnInit, OnDestroy {
  @Input() tabs: MenuTab[];
  colClassNames: String[];

  constructor(private iconReg: SvgIconRegistryService) {
  }

  ngOnInit() {
    for (let i = 0; i < this.tabs.length; i++) {
      const tab = this.tabs[i];
      const svgPath = 'assets/icon/' + tab.icon  + '.svg';
      this.iconReg.loadSvg(svgPath);
    }
    let className = 'col-xs-12';
    switch (this.tabs.length) {
      case 1:
        className = 'col-xs-12';
        break;
      case 2:
        className = 'col-xs-6';
        break;
      case 3:
        className = 'col-xs-4';
        break;
      case 4:
        className = 'col-xs-3';
        break;
      default:
        className = 'col-xs-3';
        break;
    }
    this.colClassNames = [className, 'tab'];
  }

  ngOnDestroy() {
    for (let i = 0; i < this.tabs.length; i++) {
      const tab = this.tabs[i];
      const svgPath = 'assets/icon/' + tab.icon  + '.svg';
      this.iconReg.unloadSvg(svgPath);
    }
  }

}



