import { Component, OnInit, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { SvgIconRegistryService } from 'angular-svg-icon';
import { MenuTab } from '../../class/menu-tab';

@Component({
  selector: 'app-menu-list',
  templateUrl: './menu-list.component.html',
  styleUrls: ['./menu-list.component.css']
})
export class MenuListComponent implements OnInit, OnDestroy {
  @Input() list: MenuTab[];
  @Output() click = new EventEmitter();

  constructor(private iconReg: SvgIconRegistryService) { }

  ngOnInit() {
    for (let i = 0; i < this.list.length; i++) {
      const item = this.list[i];
      const svgPath = 'assets/icon/' + item.icon  + '.svg';
      this.iconReg.loadSvg(svgPath);
    }
  }

  ngOnDestroy() {
    for (let i = 0; i < this.list.length; i++) {
      const item = this.list[i];
      const svgPath = 'assets/icon/' + item.icon  + '.svg';
      this.iconReg.unloadSvg(svgPath);
    }
  }

  onClick(item: MenuTab) {
    this.click.next(item);
    // this.settings.setTitle(item.title);
  }
}



